
.operate-liquidity {
  height: 410px;
  font-weight: 600;
  position: relative;
  .remove-hint{
    padding-left: 24px;
    padding-top: 10px;
    position: absolute;
    top: 112px;
  }
  .add-input {
    display: flex;
    justify-content: center;
    padding-top: 35px;
  }

  .wallet-balance {
    padding-left: 24px;
    padding-top: 8px;
  }

  .check-proportion {
    width: 332px;
    height: 40px;
    margin: 0 auto;
    margin-top: 32px;
  }

  .information {
    width: 332px;
    height: 72px;
    margin: 0 auto;
    margin-top: 32px;

    .percentage,
    .staked-balance {
      display: flex;
      height: 40px;
      justify-content: space-between;
      font-size: 14px;
      align-items: center;
      .info-box-num{
        color: #E0ECFF;
      }
    }
    .isBTokenAdequate{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 16px;
      border: 1px solid rgba(206, 94, 94, 0.2);
      font-size: 12px;
      height: 64px;
      img{
        margin-right: 10px;
      }
    }
  }

  .add-remove-btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 19px;
    .magrined{
      font-size: 14px;
      color:#E0ECFF ;
      border-bottom: 1px solid #FFF;
      margin-bottom: 4px;
      cursor: pointer;
    }
  }
}